import './App.css'
import { Component } from 'react'

export default class App extends Component {
  state = {
    coinA: 'BTC',
    coinB: 'BRL',
    valA: 1,
    valB: 1
  }

  attr = {
    loaded: false,
    interval: undefined
  }

  returnValue(json, el = document.querySelector('#input-a')) {
    document.title = Number(json.lastPrice).toLocaleString('pt-BR', {minimumFractionDigits: 2, maximumFractionDigits: 2})
    if (el.nextElementSibling.id === 'sideA') {
      this.setState({
        valA: el.value,
        valB: Number(el.value * json.lastPrice).toLocaleString('pt-BR', {minimumFractionDigits: 2, maximumFractionDigits: 2})
      }, () => document.querySelector('#input-b').value = this.state.valB)
      
    } else {
      this.setState({
        valA: (1 / (json.lastPrice / el.value)).toFixed(4),
        valB: el.value
      }, () => document.querySelector('#input-a').value = this.state.valA)
      
    }
  }

  fetchUrl(el = document.querySelector('#input-a')) {
    fetch(
      `https://api.binance.com/api/v3/ticker/24hr?symbol=${this.state.coinA}${this.state.coinB}`
    )
      .then(res => res.json())
      .then(json => this.returnValue(json, el))
  }

  getValue(el = document.querySelector('#input-a')) {
    if (this.attr.interval !== undefined) clearInterval(this.attr.interval)
    this.attr.interval = setInterval(() => {
      this.fetchUrl(el)
    }, 2000)
  }

  calc(event = null) {
    const el = event.target

    this.fetchUrl(el)

    this.getValue(el)
  }

  componentDidMount() {
    if (this.attr.loaded) {
      return
    } else {
      this.fetchUrl()

      this.attr.loaded = true
      this.getValue()
    }
  }

  componentDidUpdate(){
    this.getValue()
  }

  

  render() {
    return (
      <div className='container'>
        <header>
          <h1>Conversor de criptomoedas</h1>
        </header>
        <div>
        <input
          id="input-a"
          defaultValue={this.state.valA}
          type="number"
          onChange={e => this.calc(e)}
        ></input>
        <select className = "fab"  onClick={e => {this.setState({coinA: e.target.value})}} id="sideA">
          <option value="BTC">BTC &#xf379;</option>
          <option value="ETH">ETH &#xf42e;</option>
          <option value="BNB">BNB</option>
          <option value="DOGE">DOGE</option>
          <option value="ADA">ADA</option>
          <option value="LTC">LTC</option>
          <option value="SOL">SOL</option>
          {/*<option value="SHIB">SHIB</option>*/}
          <option value="CAKE">CAKE</option>
          <option value="AXS">AXS</option>
          <option value="XRP">XRP</option>
          <option value="CHZ">CHZ</option>
          <option value="DOT">DOT</option>
          <option value="LINK">LINK</option>
        </select>
        </div>
        <i className="fas fa-equals"></i>
        <div>
        <input
          defaultValue={this.state.valB}
          id="input-b"
          type="text"
          onChange={e => this.calc(e)}
        ></input>
        <select className = "fab" onClick={e => {this.setState({coinB: e.target.value})}} id="sideB">
          <option  value="BRL">BRL</option>
          <option value="USDT">USDT</option>
        </select>
        </div>
      </div>
    )
  }
}
